html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-cart {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-dots {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

@media only screen and (max-width: 1020px) {
  .container {
    margin: 0 20px;
  }
}

.wrap {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.hero {
  height: 100%;
}

.header__logo {
  display: block;
  margin: 30px auto;
}

.header__bottom {
  border-top: 1px solid #eee;
}

.header__bottom .container {
  padding: 15px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header__menu ul li {
  display: inline-block;
  padding: 0 20px;
}

.header__menu ul li:first-child {
  padding-left: 0;
}

.header__menu ul li a {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  color: #170F11;
}

.header__menu ul li a:after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: #170F11;
  transition: width .53s;
}

.header__menu ul li a:hover::after {
  width: 100%;
  transition: width .53s;
}

.header__icons a {
  text-decoration: none;
}

.header__icons .icon {
  fill: #000;
  font-size: 24px;
  margin-left: 20px;
}

.header__icons .cart {
  position: relative;
}

.header__icons .cart-number {
  position: absolute;
  top: -15px;
  right: -9px;
  border-radius: 50%;
  border: 2px solid #fff;
  color: #fff;
  background: #000;
  font-size: 10px;
  padding: 1px 5px;
}

.hero .container {
  position: relative;
  color: #a9765a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  align-items: start;
  padding-left: 0;
  text-align: left;
}

.hero__suptitle {
  font-size: 47px;
  margin-bottom: 25px;
  margin-top: 25px;
  text-transform: uppercase;
  font-weight: normal;
}

@media only screen and (max-width: 900px) {
  .hero__suptitle {
    font-size: 45px;
  }
}

@media only screen and (max-width: 550px) {
  .hero__suptitle {
    font-size: 30px;
    margin-bottom: 15px;
  }
}

@media only screen and (max-width: 410px) {
  .hero__suptitle {
    font-size: 25px;
  }
}

.hero__title, .hero__title > span {
  font-size: 77px;
  font-weight: normal;
  line-height: 1;
  text-transform: uppercase;
}

@media only screen and (max-width: 900px) {
  .hero__title, .hero__title > span {
    font-size: 68px;
    margin-bottom: 25px;
  }
}

@media only screen and (max-width: 550px) {
  .hero__title, .hero__title > span {
    font-size: 50px;
  }
}

@media only screen and (max-width: 410px) {
  .hero__title, .hero__title > span {
    font-size: 40px;
  }
}

.hero__title {
  margin-bottom: 35px;
}

@media only screen and (max-width: 725px) {
  .hero__title {
    text-shadow: 0 0 10px #fff, 0 0 10px #fff, 0 0 10px #fff;
  }
}

.hero__title span {
  color: #170f11;
}

.hero__text {
  font-size: 16px;
  line-height: 1.7;
  margin-bottom: 70px;
  max-width: 60%;
}

@media only screen and (max-width: 410px) {
  .hero__text {
    max-width: 90%;
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 120px;
  }
}

button {
  margin-top: 20px;
  position: absolute;
  bottom: 30px;
  display: inline-block;
  color: #fff;
  background: #170F11;
  text-decoration: none;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: normal;
  line-height: 1;
  padding: 15px 30px;
  text-align: center;
  width: 180px;
  transition: 1s;
}

button:before, button:after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  top: 0;
  filter: blur(30px);
}

button:before {
  background: rgba(255, 255, 255, 0.5);
  width: 60px;
  left: 0;
  opacity: .5;
  transform: translate(-130px) skewX(-15deg);
}

button:after {
  background: rgba(255, 255, 255, 0.2);
  width: 30px;
  left: 30px;
  opacity: 0;
  transform: translate(-100px) scaleX(-15px);
}

button:hover {
  background: #a9765a;
  cursor: pointer;
}

button:hover:before, button:hover:after {
  transform: translateX(200px) skewX(-15deg);
  opacity: .6;
  transition: .7s;
}

button:hover:after {
  opacity: 1;
}

@media screen and (max-width: 864px) {
  .header__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: #fff;
    opacity: 0;
    transition: all .8s ease;
  }
  .header__menu ul {
    position: absolute;
    top: 50%;
    right: 0;
    background: #FFF;
    transform: translateY(-50%);
  }
  .header__menu ul li {
    display: block;
    float: none;
    text-align: right;
    margin-bottom: 10px;
  }
  .header__menu ul li:nth-child(1) a {
    transition-delay: 0.2s;
  }
  .header__menu ul li:nth-child(2) a {
    transition-delay: 0.3s;
  }
  .header__menu ul li:nth-child(3) a {
    transition-delay: 0.4s;
  }
  .header__menu ul li:nth-child(4) a {
    transition-delay: 0.5s;
  }
  .header__menu ul li:nth-child(5) a {
    transition-delay: 0.6s;
  }
  .header__menu ul li:nth-child(6) a {
    transition-delay: 0.65s;
  }
  .header__menu ul li:nth-child(7) a {
    transition-delay: 0.7s;
  }
  .header__menu ul li:not(:first-child) {
    margin-left: 0;
  }
  .header__menu ul li a {
    padding: 10px 24px;
    opacity: 0;
    color: #a9765a;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1.2px;
    transform: translateX(-20px);
    transition: all 0.2s ease;
    display: inline-block;
  }
  .nav-btn {
    position: fixed;
    right: 10px;
    top: 10px;
    display: block;
    width: 48px;
    height: 48px;
    cursor: pointer;
    z-index: 9999;
    border-radius: 50%;
    background: #eee;
  }
  .nav-btn i {
    display: block;
    width: 20px;
    height: 2px;
    background: #000;
    border-radius: 2px;
    margin-left: 14px;
  }
  .nav-btn i:nth-child(1) {
    margin-top: 16px;
  }
  .nav-btn i:nth-child(2) {
    margin-top: 4px;
    opacity: 1;
  }
  .nav-btn i:nth-child(3) {
    margin-top: 4px;
  }
}

#nav:checked + .nav-btn {
  transform: rotate(45deg);
}

#nav:checked + .nav-btn i {
  background: #000;
  transition: transform 0.2s ease;
}

#nav:checked + .nav-btn i:nth-child(1) {
  transform: translateY(6px) rotate(180deg);
}

#nav:checked + .nav-btn i:nth-child(2) {
  opacity: 0;
}

#nav:checked + .nav-btn i:nth-child(3) {
  transform: translateY(-6px) rotate(90deg);
}

#nav:checked ~ .header__menu {
  z-index: 9990;
  opacity: 1;
}

#nav:checked ~ .header__menu ul li a {
  opacity: 1;
  transform: translateX(0);
}

.hidden {
  display: none;
}
